.Judul{
    text-align: center;
}
.Container{
    max-width: 70%;
    padding-left: 15%;
}
.thumbnail{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 74%;
}
.thumbnail img{
    width:700px;
}
@media screen and (max-width:480px){
    .Judul h1{
        text-align: center;
        font-size: small;
    }
    .thumbnail img{
        width:210px;
    }
    .Container{
        padding-left: 11%;
        max-width: 80%;
    }

}